<template>
  <div>
    <div class="pageMain">
      <van-cell-group class="contentBox" v-if="info">
        <van-cell
          title="工资月份"
          :value="(info.startMonth ? info.startMonth : '-') + '月'"
        ></van-cell>
        <van-cell title="姓名" :value="info.name ? info.name : '-'"></van-cell>
        <van-cell
          title="考勤天数"
          :value="(info.attendance ? info.attendance : '-') + '天'"
        ></van-cell>
        <van-cell
          title="计量单位"
          :value="info.unit ? info.unit : '-'"
        ></van-cell>
        <van-cell
          title="计量单价"
          :value="info.price ? info.price : '-'"
        ></van-cell>
        <van-cell
          title="应发金额"
          :value="info.shouldPay ? info.shouldPay : '-'"
        ></van-cell>
        <van-cell
          title="实发金额"
          :value="info.bankactPay ? info.bankactPay : '-'"
        ></van-cell>
        <van-cell
          title="工资卡开户行"
          :value="info.payAccountBank ? info.payAccountBank : '-'"
        ></van-cell>
        <van-cell
          title="银行卡号"
          :value="info.accountNumber ? info.accountNumber : '-'"
        ></van-cell>
        <van-cell title="状态">
          <template>
              <span v-if="info.showState == '待确认'" class="rightValueY">{{info.showState}}</span>
                <span v-if="info.showState == '待发放'" class="rightValueY">{{info.showState}}</span>
                <span v-if="info.showState == '已拒绝'" class="rightValueY">{{info.showState}}</span>
                <span v-if="info.showState == '已发放'" class="rightValueB">{{info.showState}}</span>
          </template>
        </van-cell>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: null,
    };
  },
  methods: {
    getSalaryDetail(id) {
      this.$http.jsb.getSalaryDetail({ id }).then((res) => {
        if (res.code == 200) {
          this.info = res.data;
          var stateCode = this.info.stateCode;
          var bankStatus = this.info.bankStatus;
          var state = this.info.state;
          if (stateCode == "APPROVED" && state == "0") {
            this.info.showState = "待确认";
          }
          if (
            (stateCode == "APPROVED" && state == "1") ||
            stateCode == "CONFIRMED" ||
            (stateCode == "APPROVED" && bankStatus == "NO")
          ) {
            this.info.showState = "待发放";
          }
          if (
            state == "2"
          ) {
            this.info.showState = "已拒绝";
          }
          if (stateCode == "APPROVED" && bankStatus == "YES") {
            this.info.showState = "已发放";
          }
        }
      });
    },
  },
  created() {
    let id = this.$route.params.id;
    this.getSalaryDetail(id);
  },
};
</script>

